@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0%;
  overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media(prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.body-page{background-color:#eee;min-height:100vh}
.navbar{z-index:1;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:100vw;height:8vh;background-color:#333;color:#fff;position:fixed;top:0;font-family:"Apple SD Gothic Neo",Helvetica,sans-serif;box-shadow:0px 3px 6px rgba(0,0,0,.3)}.navcontainer-left{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;padding-left:5%;width:50%;font-size:larger}.navcontainer-left>p{margin-left:5%}.logo-btn{max-width:16%;display:block}.logo{max-height:90%;box-sizing:border-box}.navcontainer-right{display:-webkit-flex;display:flex;-webkit-flex-direction:row-reverse;flex-direction:row-reverse;padding-right:5%;width:50%;-webkit-flex-wrap:wrap;flex-wrap:wrap;height:100%;margin:0%;-webkit-align-items:center;align-items:center;color:#fff;list-style-type:none}.nav-link{top:0%;display:block;color:#fff;text-decoration:none;font-size:larger;text-align:center;padding-left:4%;padding-right:4%;height:100%;box-sizing:border-box}.nav-link:hover{background-color:#fff;color:#000;transition:background-color .3s ease,color .3s ease}.selected{border-bottom:5px solid #fff}
.Portfolio-content-left{width:50%;height:100%;background-color:#fefefe}.Portfolio-content-middle{width:0%;height:100%;background-color:#fefefe}.Portfolio-content-right{width:50%;height:100%;background-color:#fefefe}.display-content{padding:4%;font-size:larger}h4{font-size:1.5em}
h4{font-family:"Inconsolata",monospace;margin-top:5vh}.content{font-family:"Inconsolata",monospace;margin-top:5vh}.img-content{margin:auto;margin-top:5%;margin-bottom:5%}
.HomePage-page{left:0;width:100vw;height:100vh;display:-webkit-flex;display:flex;overflow:hidden;position:absolute}.parent-block{left:0;width:100vw;height:100vh;display:-webkit-flex;display:flex}.portfolio-content-page{margin-left:20vw;width:60vw;background-color:#fefefe;display:-webkit-flex;display:flex;margin:auto;overflow:hidden;cursor:pointer}.myearth{position:absolute;top:55vh;left:0vw;width:100vw;height:100vw;overflow:hidden;-webkit-animation-name:selfrotate;animation-name:selfrotate;-webkit-animation-duration:180s;animation-duration:180s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear}@-webkit-keyframes selfrotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes selfrotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
.block-node{display:-webkit-flex;display:flex;background-color:#eee}.end-block{margin:auto;-webkit-transform:rotateX(90deg);transform:rotateX(90deg);-webkit-animation-name:flipback;animation-name:flipback;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out;background-color:#111;opacity:0}.colored-block{margin:auto;-webkit-transform:rotateX(90deg);transform:rotateX(90deg);-webkit-animation-name:flipback;animation-name:flipback;-webkit-animation-duration:.4s;animation-duration:.4s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out;opacity:.75;box-shadow:0 3px 6px 0 rgba(66,66,66,.16),0 3px 6px 0 rgba(66,66,66,.16);border-radius:5px}.theme-red{background-color:#d16b6b}.theme-green{background-color:#47ad9a}.theme-blue{background-color:#79b2cb}.white{background-color:#fefefe}.hovering{-webkit-animation-name:flip;animation-name:flip;-webkit-animation-duration:.4s;animation-duration:.4s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes flip{from{-webkit-transform:rotate3d(1, 0, 0, 0deg);transform:rotate3d(1, 0, 0, 0deg)}to{-webkit-transform:rotate3d(1, 0, 0, 90deg);transform:rotate3d(1, 0, 0, 90deg)}}@keyframes flip{from{-webkit-transform:rotate3d(1, 0, 0, 0deg);transform:rotate3d(1, 0, 0, 0deg)}to{-webkit-transform:rotate3d(1, 0, 0, 90deg);transform:rotate3d(1, 0, 0, 90deg)}}@-webkit-keyframes flipback{from{-webkit-transform:rotate3d(1, 0, 0, 90deg);transform:rotate3d(1, 0, 0, 90deg)}to{-webkit-transform:rotate3d(1, 0, 0, 0deg);transform:rotate3d(1, 0, 0, 0deg)}}@keyframes flipback{from{-webkit-transform:rotate3d(1, 0, 0, 90deg);transform:rotate3d(1, 0, 0, 90deg)}to{-webkit-transform:rotate3d(1, 0, 0, 0deg);transform:rotate3d(1, 0, 0, 0deg)}}

.card-about{background-color:#fff;width:100%;height:85%;box-shadow:0px 3px 6px rgba(0,0,0,.3);display:-webkit-flex;display:flex}.card-left{max-width:45%;max-height:100%;padding:10% 5% 10% 5%}.card-right{width:50%;height:100%}.container-icons{margin-top:10%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.container-icons>a{padding:0% 5% 0% 5%}.container-icons>a>img{-webkit-filter:drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.16));filter:drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.16))}.container-icons>a>img:hover{-webkit-filter:drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.7));filter:drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.7))}
.navtitle{left:0%;font-family:"Inconsolata",monospace;text-align:center;margin-left:2%;height:100%}.navitem{right:0;font-family:"Inconsolata",monospace;text-align:center;margin-right:2%;height:100%}
.card{box-shadow:0 1px 3px rgba(0,0,0,.12),0 1px 2px rgba(0,0,0,.24);width:60%;height:50%;min-width:600px;min-height:400px;margin-left:25%;margin-top:5%;margin-bottom:5%;background-color:#fff;border-radius:1%;transition:all .3s cubic-bezier(0.25, 0.8, 0.25, 1)}.card:hover{box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22)}.borderlesscard{width:30%;height:30%;min-width:300px;min-height:200px;margin-left:1%;margin-right:1%;margin-top:3%;margin-bottom:1%;box-sizing:border-box;border:1px solid transparent;transition:border .1s ease}.borderlesscard:hover{border:1px solid #ccc}.card-1{box-shadow:0 1px 3px rgba(0,0,0,.12),0 1px 2px rgba(0,0,0,.24)}.card-1:hover{box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22)}
.portfolio-page{padding-top:8vh;width:100%;background-color:#eee;padding-bottom:1%;position:static;overflow:auto}.float-nav{position:fixed;width:8%;height:65%;margin-top:8%;margin-left:5%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.floatnav-item{width:60%;height:20%;text-align:center;padding:20% 20% 20% 20%;color:#999;font-size:larger;text-shadow:0px 3px 6px rgba(0,0,0,.16);border-right:3px solid #999;text-decoration:none;cursor:pointer}.floatnav-item:hover{color:#666}.header-blank{min-height:10vh;background-color:#fff;box-shadow:0px 3px 6px rgba(0,0,0,.16)}.ux-card{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;padding:5%;position:relative}.ux-card>div{margin:auto;margin-left:5%}.ux-card>img{border-radius:4%}.onFocus{color:#d16b6b;border-right:3px solid #d16b6b}.onFocus:hover{color:#d16b6b;border-right:3px solid #d16b6b}.label-btn{right:0%;bottom:0%;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;text-align:center;position:absolute;font-family:"Apple SD Gothic Neo",Helvetica,sans-serif;color:#28546f}.label-btn>p{font-size:large}.icon-btn{max-width:25%;max-height:25%;margin-left:5%;opacity:.8;cursor:pointer;transition:all .1s ease-in-out;-webkit-filter:drop-shadow(0px 0px 0px black);filter:drop-shadow(0px 0px 0px black)}.icon-btn:hover{max-width:26%;max-height:26%;opacity:1}
.topics-page{background-color:#fff;width:100%;height:85%;box-shadow:0px 3px 6px rgba(0,0,0,.3);display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding:5% 5% 5% 5%;padding-left:8%;box-sizing:border-box;padding-top:8vh}
