@import "variables.scss";

.portfolio-page{
    padding-top: $navbar-height;
    width: 100%;
    background-color: #eeeeee;
    padding-bottom: 1%;
    position: static;
    overflow: auto;
}

.float-nav{
    position: fixed;
    width: 8%;
    height: 65%;
    margin-top: 8%;
    margin-left: 5%;
    display: flex;
    flex-direction: column;
}

.floatnav-item{
    width: 60%;
    height: 20%;
    text-align: center;
    padding: 20% 20% 20% 20%;
    color: #999999;
    font-size: larger;
    text-shadow: 0px 3px 6px rgba($color: #000000, $alpha: .16);
    border-right: 3px solid #999999;
    text-decoration: none;
    cursor: pointer;
}
.floatnav-item:hover{
    color: #666666;
}


.header-blank{
    min-height: 10vh;
    background-color: white;
    box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
}

.ux-card{
    display: flex;
    flex-direction: row;
    padding: 5%;
    position:relative;
}



.ux-card>div{
    margin: auto;
    margin-left: 5%;
}

.ux-card > img{
    border-radius: 4%;
}

.onFocus{
    color: $theme-red;
    border-right: 3px solid $theme-red;
}

.onFocus:hover{
    color: $theme-red;
    border-right: 3px solid $theme-red;
}

.label-btn{
    right: 0%;
    bottom: 0%;
    display: flex;
    flex-direction: row;
    text-align: center;
    position: absolute;
    font-family: "Apple SD Gothic Neo", Helvetica, sans-serif;
    color: #28546f;
}

.label-btn > p{
    font-size: large;
}

.icon-btn{
    max-width: 25%;
    max-height: 25%;
    margin-left: 5%;
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
    filter: drop-shadow(0px 0px 0px rgba(0,0,0,1));
}

.icon-btn:hover{
    max-width: 26%;
    max-height: 26%;
    opacity: 1;


}